import Vue from 'vue'
import Router from 'vue-router'
const Home =(resolve) => {
  import('@/components/home/home').then((module) => {
    resolve(module)
  })
};
const AboutUs =(resolve) => {
  import('@/components/about-us/about-us').then((module) => {
    resolve(module)
  })
};
const Case =(resolve) => {
  import('@/components/case/case').then((module) => {
    resolve(module)
  })
};
const Detail =(resolve) => {
  import('@/components/detail/detail').then((module) => {
    resolve(module)
  })
};
const Exclusive =(resolve) => {
  import('@/components/exclusive/exclusive').then((module) => {
    resolve(module)
  })
};
const Service =(resolve) => {
  import('@/components/service/service').then((module) => {
    resolve(module)
  })
};
const News =(resolve) => {
  import('@/components/news/news').then((module) => {
    resolve(module)
  })
};
const Message =(resolve) => {
  import('@/components/message/message').then((module) => {
    resolve(module)
  })
};
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/about-us',
      name: 'about-us',
      component: AboutUs,
      meta: {
        title: '关于我们'
      }
    },
    {
      path: '/message',
      name: 'message',
      component: Message,
      meta: {
        title: '留言'
      }
    },
    {
      path: '/case',
      name: 'case',
      component: Case,
      meta: {
        title: '客户案例'
      }
    },
    {
      path: '/detail',
      name: 'detail',
      component: Detail,
      meta: {
        title: '详情'
      }
    },
    {
      path: '/exclusive',
      name: 'exclusive',
      component: Exclusive,
      meta: {
        title: '专属定制'
      }
    },
    {
      path: '/service',
      name: 'service',
      component: Service,
      meta: {
        title: '服务保障'
      }
    },
    {
      path: '/news',
      name: 'news',
      component: News,
      meta: {
        title: '新闻'
      }
    },
    {
      path: '*',
      name: 'home',
      component: Home,
      meta: {
        title: '首页-定制银币/定制金币/金银币定制/造币厂/定做金银纪念币/金银纪念章定制/订做金银条-御泉黄金官网'
      }
    },
  ]
})



