<template>
  <div class="go-top" @click="goTop">
    返回顶部
  </div>
</template>
<script type="text/ecmascript-6">
  export default {
    methods:{
      goTop(){
        document.documentElement.scrollTop = document.body.scrollTop =0;
      }
    }
  }
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">

  .go-top
    width:30px;
    height:40px;
    padding:20px;
    font:14px/20px arial;
    text-align:center;
    background:#666;
    position:fixed;
    cursor:pointer;
    bottom 0
    right 0
    color:#fff


</style>



