<template>
  <div class="header-wrap">

    <div class="header">
      <span class="logo">
        <img src="../../common/images/logo.jpg"/>
      </span>

      <div class="header-contact">
        <span class="tel">400-000-00</span>
        <span class="tel-desc">全国定制热线</span>
      </div>
    </div>
    <div class="box-nav-wrap">
    <div class="box-nav">
      <div class="nav-sort">
        <div class="nav-title"><span style="padding: 0 40px 0 20px">产品分类</span> <span class="nav-sort-icon"></span></div>
        <div class="nav-sort-box">
          <ul @click="goCase(1)" style="cursor: pointer">
            <li>金银币/章定制</li>
            <li>纯金纪念币/章</li><li>纯银纪念币/章</li><li>银镶金纪念币</li><li>银镀金/银纪念币</li><li>铜镀金/银纪念币</li>
            <li>纯铜纪念币</li>
          </ul>
          <ul @click="goCase(2)"  style="cursor: pointer">
          <li> 金银条/钞/定制</li>
          <li>金银条/金银砖</li><li>金银钞/VIP卡/名片</li><li>金银钥匙/邮票</li><li>金银书签</li>
        </ul>
          <ul @click="goCase(3)"  style="cursor: pointer">
            <li>金银奖牌/奖章/徽章/定制</li>
            <li>纯金奖牌/奖章/徽章</li><li>纯银奖牌/奖章/徽章</li><li>纯银镀金奖牌/奖章/徽章</li><li>铜质奖牌/奖章/徽章</li><li>锌合金镀金奖牌/奖章/徽章</li><li>其他材质</li>
          </ul>
          <ul @click="goCase(4)"  style="cursor: pointer">
            <li>金银摆件定制</li>
            <li>金银币镶水晶摆件</li><li>金银币镶红木摆件</li><li>金银币邮票册</li><li>金银盘</li>
          </ul>
          <ul>
            <li>包装礼盒定制</li>
            <li>红木包装盒</li><li>原木包装盒</li><li>纸质包装盒</li><li>皮质包装盒</li><li>绒布包装盒</li>
          </ul>
          <ul>
            <li>应用场景</li>
            <li>周年纪念</li><li>项目纪念</li><li>员工奖励</li><li>答谢客户</li><li>企业上市</li><li>开业庆典</li><li>竣工庆典</li><li>企业年会</li>
          </ul>
        </div>

      </div>

      <div class="nav-page">
        <ul>
          <li><a><router-link to="/home">首页</router-link></a></li>
          <li><a href="https://www.taobao.com">天猫商城</a></li>
          <li><a><router-link to="/exclusive">专属定制</router-link></a></li>
          <li><a><router-link to="/case">客户案例</router-link></a></li>
          <li><a><router-link to="/service">服务保障</router-link></a></li>
          <li><a><router-link to="/about-us">关于我们</router-link></a></li>
        </ul>
      </div>



    </div>

    </div>


  </div>
</template>
<script>
  export default {
    data(){
      return{

      }
    },
    methods:{

      goCase(product_type){
        //location.reload();

        if(window.location.href.indexOf('case') > -1){
          this.$router.push({
            path:'/case',
            query:{
              product_type : product_type
            }
          });
          location.reload();
        }else {
          this.$router.push({
            path:'/case',
            query:{
              product_type : product_type
            }
          });
        }

       // setTimeout(location.reload(),500)


      }
    }
  }

</script>
<style lang="stylus" rel="stylesheet/stylus">
  .header-wrap
    background-color #fff
    .header
      height 168px
      width 1200px
      max-width 1200px
      margin 0 auto
      .logo
        display inline-block
        padding-top 50px
        float left
      .header-contact
        float right
        margin-top 50px
        padding-left 60px
        background url(../../common/images/icon-tel.gif) no-repeat
        background-size 51px
        span
          display block
          &.tel
            font-size 24px
            line-height 30px
            color #976e01
          &.tel-desc
            font-size 18px
            line-height 20px
            color #505050
    .box-nav-wrap
      background-color #000
      .box-nav
        height 60px
        width 1200px
        max-width 1200px
        margin 0 auto
        font-size 22px
        line-height 60px
        .nav-sort
          width 200px
          height 60px
          line-height 60px
          float left
          color #fff
          position relative
          .nav-title
            background-color #976e01
            cursor pointer
            .nav-sort-icon
              background url("../../common/images/home/icon-sort.png") no-repeat center center
              background-size 17px
              width 17px
              height 17px
              display inline-block
          .nav-sort-box
            display none
            font-size 16px
          &:hover
            .nav-sort-box
              background-color #f0f0f0
              position absolute
              background rgba(255, 255 ,255, 0.9)
              z-index 999
              display block
              top 60px
              left 0
              width 1200px
              padding 30px 0 30px 0
              ul
                width 200px
                float left
                li
                  font-weight bold
                  font-size 14px
                  display block
                  line-height 50px
                  padding-left 20px
                  color #666
                  &:first-child
                    font-size 16px
                    color #000
        .nav-page
          float left
          line-height 60px
          li
            padding 0 35px
            text-align center
            display inline-block
            a
              color #fff
</style>





